import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import reportWebVitals from './reportWebVitals';
import 'antd/dist/antd.css';
import './static/style/pages/comm.less'
import { registerMicroApps, start, initGlobalState, MicroAppStateActions  } from 'qiankun';
import {BrowserRouter} from 'react-router-dom'


const state = {
  name: 'qiankun-react'
}
// 初始化 state
const actions: MicroAppStateActions = initGlobalState(state);
actions.onGlobalStateChange((state, prev) => {
  // state: 变更后的状态; prev 变更前的状态
  console.log(state, prev, 9);
});
setTimeout(() => {
  actions.setGlobalState({...state, test: 666666})
}, 2000)
actions.offGlobalStateChange();

// 使用qiankun微框架
registerMicroApps([
  {
    name: 'adminApp', // 应用名字
    // entry: '/react/', // html
    entry: '//localhost:3011', // html
    container: '#admin', // 容器名
    activeRule: '/admin', // 激活的路径
  },
  {
    name: 'react-pro', // 应用名字
    // entry: '/react/', // html
    entry: '//localhost:3012', // html
    container: '#react2', // 容器名
    activeRule: '/react2', // 激活的路径
    props: {
      name: 'test-react2',
      test: 99999999
    }
  },
]);
start();

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </React.StrictMode>
);

reportWebVitals();
